import React from 'react';
import PropTypes from 'prop-types';
import { Panel, ListItem, Group, Div, Avatar, PanelHeader, List, Cell, Counter } from '@vkontakte/vkui';

const Home = ({ id, fetchedUser }) => (
	<Panel id={id}>
		<PanelHeader>Информация об участнике</PanelHeader>
		{fetchedUser &&
		<Group title="User Data Fetched with VK Connect">
			<ListItem
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</ListItem>
		</Group>}

		<Group title="Баллы">
			<List>
				<Cell indicator={<Counter type="primary">1</Counter>}>Друзья</Cell>
			</List>
		</Group>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
	userData: PropTypes.shape({
		photo: PropTypes.string,
		username: PropTypes.string,
		update: PropTypes.string,
		text: PropTypes.string
	}),
};

export default Home;